.flip {
    -webkit-perspective: 800px;
    width: 14em;
    /*height: 200px;*/
    position: -webkit-sticky;
    position: sticky;
    margin-bottom: 10px;
}

.flip .card.flipped {
    -webkit-transform: rotatex(-180deg);
}

.flip .card {

    width: 100%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
    -webkit-transition: 0.5s;
}

.card img {
    width: 100%;
}

